@font-face {
    font-family: 'Folkard';
    src: url('./fonts/folkard_.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}

/* üé® ESTILOS GENERALES */
.guild-container {
    min-height: 100vh;
    background-color: #121212;
    color: white;
    padding: 20px;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
}

/* üìå TITULOS */
.guild-title {
    font-family: 'Folkard', sans-serif;
    font-size: 42px;
    font-weight: bold;
    color: #ffd700;
    margin-bottom: 5px;
}

.guild-subtitle {
    font-family: 'Folkard', sans-serif;
    font-size: 28px;
    font-weight: bold;
    color: #cccccc;
    margin-top: 0;
    margin-bottom: 5px;
}

.guild-warning {
    font-family: 'Folkard', sans-serif;
    font-size: 22px;
    color: red;
    font-weight: bold;
    margin-top: 0;
    margin-bottom: 20px;
}

/* üìå CONTENEDORES PRINCIPALES */
.sidebars {
    display: flex;
    justify-content: space-between;
    width: 100%;
    max-width: 1200px;
    margin-top: 20px;
    gap: 20px;
}

/* üìå BARRAS LATERALES */
.left-sidebar, .right-sidebar {
    width: 22%;
    text-align: left;
    padding: 10px;
    background-color: #222;
    border-radius: 8px;
}

/* üìå TABLA */
.table-container {
    width: 50%;
    text-align: center;
    background-color: #222;
    border-radius: 8px;
    padding: 10px;
}

/* üìå TABLA - ESTILOS */
.guild-table {
    width: 100%;
    border-collapse: collapse;
    border: 2px solid #555;
    background-color: #222;
}

.guild-table th,
.guild-table td {
    border: 1px solid #444;
    padding: 10px;
    text-align: center;
}

.guild-table th {
    background-color: #333;
    color: #ddd;
    font-weight: bold;
}

.guild-table tr:hover {
    background-color: #444;
}

/* üìå IM√ÅGENES */
.role-image {
    width: 40px;
    height: 40px;
    border-radius: 50%;
}

/* üìå TEXTO TWITCH */
.twitch-text {
    color: #9146ff;
    font-weight: bold;
}

/* üìå LISTAS */
.role-list, .class-list, .profession-list {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.role-list div, .class-list div, .profession-list div {
    color: #ddd;
    margin-bottom: 10px;
}

/* üì± RESPONSIVE DESIGN */
@media (max-width: 1024px) {
    .sidebars {
        flex-direction: column;
        align-items: center;
    }

    .left-sidebar, .right-sidebar, .table-container {
        width: 100%;
        max-width: 90%;
        margin-bottom: 20px;
    }
}

@media (max-width: 768px) {
    .guild-title {
        font-size: 36px;
    }

    .guild-subtitle {
        font-size: 24px;
    }

    .guild-warning {
        font-size: 20px;
    }

    .table-container {
        overflow-x: auto;
    }

    .guild-table {
        width: 100%;
        min-width: 600px;
    }

    .role-image {
        width: 35px;
        height: 35px;
    }
}

@media (max-width: 480px) {
    .guild-title {
        font-size: 28px;
    }

    .guild-subtitle {
        font-size: 22px;
    }

    .guild-warning {
        font-size: 18px;
    }

    .sidebars {
        flex-direction: column;
        align-items: center;
    }

    .left-sidebar, .right-sidebar {
        width: 100%;
        text-align: center;
    }

    .guild-table {
        width: 100%;
        min-width: 500px;
    }

    .role-image {
        width: 30px;
        height: 30px;
    }
}
